@import url('https://fonts.googleapis.com/css2?family=Bitter:ital,wght@0,100..900;1,100..900&family=Chakra+Petch:wght@300;400;700&display=swap');

body {
    background-color: #f0f0f0;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    height: 100vh;
    overflow: hidden;
}

#content {
    position: fixed;
    top: 50%;
    left: 50%;
    width: auto;
    height: auto;
    overflow: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(-50%, 100%);
    transition: all 0.5s;
    z-index: 2;
}

#content.fixed {
    top: 30px;
    /* backdrop-filter: blur(10px); */
    transform: translate(-50%, -50%);
    background-color: #1a1a1a;
    border-radius: 3px;
    padding-left: 10px;
    padding-right: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
}

.svg-button {
    cursor: pointer;
    transition: transform 0.3s, opacity 0.3s;
    /* margin: 5px; */
}

.svg-button text {
    fill: #fff;
    font-family: "Chakra Petch", serif;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 900;
}

.svg-button rect {
    fill: none;
    stroke: white;
    stroke-width: 1;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    transition: stroke-dashoffset 0.75s, fill 0.5s;
}

.svg-button.selected rect {
    /* stroke-dashoffset: 0; */
    fill: rgba(0,0,0,.4);
}

#content.fixed .svg-button text {
    fill: #555;
}

#content.fixed .svg-button.selected text {
    fill: white;
}

.svg-button:hover rect{
    stroke-dashoffset: 0;
}

#content.fixed .svg-button:hover text {
    fill: white;
}

#content.fixed .svg-button.selected rect {
    stroke: #444;
}

#page {
    font-family: "Chakra Petch", serif;
    position: absolute;
    top: 100%;
    left: 50%;
    width: 100%;
    min-height: 100%;
    background-color: rgba(255,255,255,.1);
    backdrop-filter: saturate(0%) brightness(0.1);
    z-index: 1;
    color: white;
    transform: translateX(-50%);
    transition: all 0.5s;
    max-height: 100vh;
    overflow-y: auto;
}

#page .inside-content {
    max-width: 650px;
    margin: 0 auto;
    margin-top: 100px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 100px;
}

h1, h2 {
    font-family: "Chakra Petch", serif;
    font-size: 2.5em;
    margin-bottom: 20px;
    text-align: center;
    text-transform: uppercase;
}

h2 {
    font-size: 1.5rem;
    text-align: left;
    margin-bottom: 5px;
}

p {
    font-size: 1.1rem;
    line-height: 1.5em;
    margin-bottom: 20px;
    font-family: "Bitter", serif;
}

#page.show {
    top: 0;
}

#page .grid-content {
    margin-top: 100px;
    margin-bottom: 100px;
    justify-content: center;
}

.grid-item {
    padding: 30px;
    transition: all 0.3s;
    display: flex;
    text-decoration: none;
    color: white;
    align-items: center;
}

.grid-item:nth-child(even) {
    background-color: rgba(0,0,0,.3);
    flex-direction: row-reverse;
}

.grid-image {
    width: 50%;
    min-height: 400px;
    background-size: cover;
    background-position: center;
    border-radius: 5px;
    display: flex;
    justify-content: flex-end;
}

.grid-image svg {
    width: 100%;
    height: 100%;
    fill: white;
    max-width: 400px;
    overflow: visible;
    cursor: pointer;
}

.grid-image svg rect:hover {
    filter: grayscale(100%) invert(1);
}

.grid-text {
    width: calc(50% - 40px);
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.grid-text h2 {
    font-size: 1.5em;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-weight: bold;
}

.grid-text p {
    font-size: 1.1em;
    line-height: 1.5em;
    margin-bottom: 10px;
    max-width: 350px;
}

.grid-item:nth-child(even) .grid-text {
    text-align: right;
    align-items: flex-end;
}

.grid-item:nth-child(even) .grid-image {
    justify-content: flex-start;
}

.grid-link {
    padding: 20px;
    position: relative;
    z-index: 1;
}

.grid-link .svg-button rect {
    fill: rgba(255,255,255,.5);
}

.grid-link .svg-button text {
    font-size: 1.5rem;
    alignment-baseline: middle;
    transform: translate(0,-2px);
    fill: #333;
}

.grid-link .svg-button:hover rect {
    fill: rgba(0,0,0,.4);
}

.grid-link .svg-button:hover text {
    fill: #fff;
}

#page {
    overflow-x: hidden;
}

img {
    margin-top: 50px;
    max-width: 1000px;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0px);
}

#page .inside-content li, #page .inside-content ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

#page .inside-content li {
    margin-bottom: 10px;
}

a {
    color: white;
    text-decoration: none;
    font-size: 1.1rem;
    background-color: rgba(0,0,0,.3);
    display: inline-block;
    padding: 5px 10px;
    transition: all 0.3s;
    border: 1px dashed rgba(255,255,255,.5);
}

a:hover {
    background-color: rgba(255,255,255,.15);
}

pre {
    white-space: pre-line;
    text-align: center;
}

pre code, p code {
    font-size: 1.25rem;
    font-family: "Chakra Petch", serif;
    font-weight: bold;
    background-color: rgba(255,255,255,.15);
    padding: 5px;
    display: inline-block;
}

.gallery-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 20px;
    background: #222;
    border-radius: 5px;
}

.gallery-btn {
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 42px;
    cursor: pointer;
    opacity: 0.5;
}

.gallery-nav .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: white;
    opacity: 0.25;
    margin: 0 5px;
    cursor: pointer;
    transition: all 0.3s;
}

.gallery-nav .dot.active {
    opacity: 1;
}

.caption {
    text-align: center;
    margin-top: 10px;
    padding: 5px;
    /* text-transform: uppercase; */
    font-size: 15px;
    font-family: "Chakra Petch", serif;
}

.svg-image svg {
    width: 100%;
    height: auto;
}

.svg-image text {
    font-family: "Chakra Petch", serif !important;
    /* font-size: 1.5rem !important; */
    font-weight: bold !important;
    /* text-transform: uppercase !important; */
    fill: white !important;
}

*::-webkit-scrollbar {
    height: 6px;
    width: 6px;
  }
  *::-webkit-scrollbar-track {
    border-radius: 20px;
    background-color: #DFE9EB;
  }
  
  *::-webkit-scrollbar-track:hover {
    background-color: #B8C0C2;
  }
  
  *::-webkit-scrollbar-track:active {
    background-color: #B8C0C2;
  }
  
  *::-webkit-scrollbar-thumb {
    border-radius: 8px;
    background-color: #474852;
  }
  
  *::-webkit-scrollbar-thumb:hover {
    background-color: #3D454A;
  }
  
  *::-webkit-scrollbar-thumb:active {
    background-color: #3D454A;
  }


@media all and (max-width: 800px) {
    .grid-text {
        width: 100%;
    }

    img {
        margin-top: 0px;
        margin-bottom: 0px;
    }
}